import FlyLine from './FlyLIne/index.js'
// 
const components = [
  FlyLine
]

//  install  Vue  use 
const install = function (Vue) {
  // 
  components.forEach(component => Vue.component(component.name, component))
}

// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}


export {
  install,
  FlyLine
}

export default {
  //  install Vue.use() 
  install,
  FlyLine
}
